import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import {Client} from '../models/Client';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ClientService {
  clientCollection: AngularFirestoreCollection<Client>;
  clientDoc:  AngularFirestoreDocument<Client>;
  clients: Observable<Client[]>;
  client: Observable<Client>;

  constructor(private afs: AngularFirestore) {
    this.clientCollection = this.afs.collection('client', ref => ref.orderBy('lastName'));
   }

   getClients(): Observable<Client[]> {
     this.clients = this.clientCollection.snapshotChanges().pipe(map(changes => {
           return changes.map(action => {
            const data = action.payload.doc.data() as Client;
            data.id = action.payload.doc.id;
            return data;
          });
      }));
     return this.clients;
   }

   public newClient(client: Client): void {
    this.clientCollection.add(client);
   }

   getClient(id: string): Observable<Client> {
     this.clientDoc = this.afs.doc<Client>(`client/${id}`);
     this.client = this.clientDoc.snapshotChanges().pipe(map(cl => {
        if (cl.payload.exists === false ) {
          return null;
        } else {
          const data = cl.payload.data() as Client;
          data.id = cl.payload.id;
          return data;
        }
     }));

     return this.client;
   }

   updateClient(client: Client): void {
     this.clientDoc = this.afs.doc(`client/${client.id}`);
     this.clientDoc.update(client);
   }

   deleteClient(client: Client): void {
    this.clientDoc = this.afs.doc(`client/${client.id}`);
    this.clientDoc.delete();
  }

}
